import { INCREMENT_COUNTER, FETCH_TEST } from '../actions/index'

const INITIAL_STATE = {
    counter: 1,
    test: null,
};

export default function(state = INITIAL_STATE, action){
    switch (action.type){
        case INCREMENT_COUNTER:
            return {...state, counter: state.counter + 1};
        case FETCH_TEST:
            return {...state, test: action.payload.data.data};
    }
    return state;
}


