import React from 'react'
import  { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { incrementCounter, fetchTest } from '../actions/index';

class Counter extends React.Component{
    render() {
        return (
            <div>
                <div className="ui labeled button" title="frontend/base/components/Counter.jsx">
                  <div className="ui red button" onClick={ () => {
                      this.props.incrementCounter();
                      this.props.fetchTest();
                  }}>
                    <i className="heart icon"/> Increase number
                  </div>
                  <a className="ui basic red left pointing label">
                      {this.props.example.counter}
                  </a>
                </div>
                <div style={{fontSize: '0.5em'}}>
                    Server data: {this.props.example.test || 'Press button for call'}
                </div>
            </div>
        );
    }
}

function mapStateToProps(state){
    return { example: state.example };
}

function mapDispatchToProps(dispatch){
    return bindActionCreators( {incrementCounter, fetchTest}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Counter)


